<template>
  <div>
    <el-button :type="isMobile ? 'text' : 'info'" :loading="isMobile ? false : initLoading" round @click="onSelect" size="mini" style="border: none; border-radius: 10px; font-size: 16px">
      <div v-if="!initLoading" style="display: flex; align-items: center; font-size: 18px;">
        <template v-if="!isMobile">
          <img referrerPolicy="no-referrer" v-if="selectTokenIcon !== ''" :src="selectTokenIcon" class="token-icon">
          <svg v-else style="margin-right: 5px;" xmlns="http://www.w3.org/2000/svg" width="24px" height="24px" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round" class="sc-1fvnadz-1 cnWwkh"><circle cx="12" cy="12" r="10"></circle><line x1="4.93" y1="4.93" x2="19.07" y2="19.07"></line></svg>
        </template>
        <span style="font-size: 15px;">{{selectTokenSymbol}}</span>
        <i class="el-icon-arrow-down" style="margin-left: 1px;"></i>
      </div>
      <span v-else>{{$t('header.selectToken')}}</span>
    </el-button>
    <el-dialog
        :title="$t('header.selectToken')"
        :visible.sync="tokenListDialogVisible"
        custom-class="wallet-dialog"
        :append-to-body="true"
        @open="onTokenListDialogOpen"
        width="90%">
      <div>
        <el-input :placeholder="$t('message.selectTokenPlaceholder')" v-model="searchText" clearable></el-input>
        <div style="max-height: 550px; overflow-y: auto; margin-top: 10px;">
          <div>
            <div v-for="(item) in poolList" :key="item.token.address" style="margin: 10px 15px;">
              <el-button @click="onTokenCardClick(item)" :loading="item.token.symbol === ''" type="default" :disabled="value === item.token.address" class="token-btn">
                <span v-if="item.token.symbol === ''">{{formatAddress(item.token.address)}}</span>
                <div v-else-if="isMobile" style="display: flex; flex-direction: column; align-items: flex-start; justify-content: space-between; font-size: 18px;">
                  <div style="display: flex; align-items: center;">
                    <img v-if="item.token.icon !== ''" :src="item.token.icon" class="token-icon">
                    <svg v-else style="margin-right: 5px;" xmlns="http://www.w3.org/2000/svg" width="24px" height="24px" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round" class="sc-1fvnadz-1 cnWwkh"><circle cx="12" cy="12" r="10"></circle><line x1="4.93" y1="4.93" x2="19.07" y2="19.07"></line></svg>
                    <span>{{item.token.symbol}}</span>
                  </div>
                  <span style="margin-top: 5px; color: #6d6c6c; font-size: 15px;">{{item.token.name}}</span>
                  <!--                  <span>{{item.token.balanceInUnit}}</span>-->
                </div>
                <template v-else>
                  <div style="display: flex; align-items: center; justify-content: space-between; font-size: 18px;">
                    <div style="display: flex; align-items: center;">
                      <img v-if="item.token.icon !== ''" :src="item.token.icon" class="token-icon">
                      <svg v-else style="margin-right: 5px;" xmlns="http://www.w3.org/2000/svg" width="24px" height="24px" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round" class="sc-1fvnadz-1 cnWwkh"><circle cx="12" cy="12" r="10"></circle><line x1="4.93" y1="4.93" x2="19.07" y2="19.07"></line></svg>
                      {{item.token.symbol}} ({{item.token.name}})
                    </div>
                    <!--                  <span>{{item.token.balanceInUnit}}</span>-->
                  </div>
                  <div v-if="item.token.symbol !== ''" style="font-size: 16px; margin-top: 5px;">
                    <a @click="goTokenView($event, item)" target="_blank" class="link-a">{{item.token.address}}</a>
                  </div>
                </template>
              </el-button>
            </div>
            <div v-if="addTokenVisible" style="margin-top: 5px; padding: 0 20px;">
              <el-input v-model="addTokenForm.tokenAddress" placeholder="0x"></el-input>
              <div style="padding: 10px 0; display: flex; justify-content: space-between;">
                <tx-commit-button :tx-loading="txLoading" style="width: 100%;" :tx-form-check="txFormCheck"></tx-commit-button>
                <el-button @click="goCancel" round :size="isMobile ? 'mini' : 'large'" style="width: 100px; margin-left: 10px;">{{$t('common.cancel')}}</el-button>
              </div>
            </div>
            <el-button v-else-if="!txLoading" type="default" class="token-btn" style="text-align: center; margin-top: 5px;" icon="el-icon-plus" @click="goAddToken">
              {{$t('addToken.form.add')}}
            </el-button>
          </div>
        </div>
      </div>
    </el-dialog>
    <el-dialog
        :visible.sync="txConfirmDialogVisible"
        custom-class="wallet-dialog"
        :append-to-body="true"
        :title="$t('common.txConfirmDialogTitle')"
        width="580px">
      <tx-confirm ref="txConfirm" v-model="hash" :tx-form="txForm" :call-func="txConfirmCall">
        <div style="width: 100%;" slot="content">
          <div class="sec_form_panel">
            <div class="sec_form_item">
              <div class="sec_form_item_title">
                <span>{{$t('addToken.form.operate')}}</span>
              </div>
              <div class="sec_form_item_content">
                {{$t('addToken.form.operateInfo')}}
              </div>
            </div>
            <div class="sec_form_item">
              <div class="sec_form_item_title">
                <span>{{$t('addToken.form.token')}}</span>
              </div>
              <div class="sec_form_item_content">
                {{addTokenForm.tokenAddress}}
              </div>
            </div>
          </div>
        </div>
      </tx-confirm>
    </el-dialog>

    <el-dialog
        :visible.sync="installWalletDialogVisible"
        custom-class="wallet-dialog"
        append-to-body
        width="90%">
      <div style="display: flex; align-items: center; justify-content: center; flex-direction: column;">
        <div class="wallet-tip" v-html="$t('common.installWalletTip')">
        </div>
        <el-button :size="isMobile ? 'mini' : 'large'" type="primary" @click="goInstallWallet">{{$t('common.installWallet')}}</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import TxCommitButton from '@/components/common/tx_commit_button';
import TxConfirm from '@/components/tx/tx_confirm'
import common from "@/utils/common"
import base from "@/utils/base"
import tx_base from "@/utils/tx_base";
import factory from "@/utils/factory";
import events from "@/utils/events";

export default {
  mixins: [base, tx_base],
  name: 'TokenSelect',
  model: {
    prop: 'selected',
    event: 'change',
  },
  props: {
    selected: String
  },
  components: {
    TxCommitButton,
    TxConfirm
  },
  data() {
    return {
      installWalletDialogVisible: false,
      tokenListDialogVisible: false,
      searchText: '',
      value: '',
      selectedPool: null,
      addTokenVisible: false,
      addTokenForm: {
        tokenAddress: '',
      },
      txConfirmDialogVisible: false,
    }
  },
  computed: {
    poolList() {
      const list = this.tokenFilter(this.searchText)
      const pendingList = this.getCreatePoolTx
      pendingList.forEach(item => {
        list.push({
          token: {
            address: item.token,
            symbol: '',
          }
        })
      })
      return list
    },
    getCreatePoolTx() {
      const txs = this.$store.state.transactions
      const list = []
      txs.forEach(item => {
        if (item.type === common.getTxTypes().createPool && common.getTxStatus(item) === 'pending') {
          list.push(item)
        }
      })
      return list
    },
    selectTokenSymbol() {
      if (null != this.poolItem) {
        let symbol = this.poolItem.token.symbol
        if (this.isMobile) {
          if (symbol.length > 10) {
            symbol = symbol.substring(0, 10) + '...'
          }
        }
        return symbol
      }
      return this.$t('header.selectToken')
    },
    selectTokenIcon() {
      if (null != this.poolItem) {
        return this.poolItem.token.icon
      }
      return ''
    }
  },
  watch: {
  },
  created() {
    this.eventHandlers[events.txSent] = sentHash => {
      if (this.hash === sentHash) {
        this.txPending = true
        this.addTokenForm.tokenAddress = ''
        this.addTokenVisible = false
      }
    }
  },
  methods: {
    onSelect() {
      if (typeof window.ethereum !== 'undefined') {
        this.tokenListDialogVisible = true
      } else {
        this.installWalletDialogVisible = true
      }
    },
    onTokenListDialogOpen() {
      this.searchText = ''
      this.value = this.tokenAddress
      this.selectedPool = this.poolItem

      this.tokenFilter('')
      this.addTokenVisible = false
      this.addTokenForm.tokenAddress = ''
    },
    tokenFilter(keyword) {
      if (keyword === '') {
        return this.$store.state.poolList
      }
      const list = []
      this.$store.state.poolList.forEach(item => {
        if (item.token.address.toLowerCase().indexOf(keyword.toLowerCase()) !== -1) {
          list.push(item)
        }
      })

      this.$store.state.poolList.forEach(item => {
        if (item.token.name.toLowerCase().indexOf(keyword.toLowerCase()) > -1) {
          list.push(item)
        }
      })

      return list
    },
    async onTokenCardClick(pool) {
      this.selectedPool = pool
      this.value = pool.token.address
      this.$emit('change', pool.token.address)
      this.tokenListDialogVisible = false
      await common.onTokenSelected(pool)
      if (this.account !== '') {
        this.$eventBus.$emit(events.tokenChanged, pool)
      }

    },
    goTokenView(event, item) {
      const url = this.getExplorerUrl('token', item.token.address)
      window.open(url, '_blank')
      event.stopPropagation()
    },
    goCancel() {
      this.addTokenVisible = false
    },
    goAddToken() {
      this.addTokenForm.tokenAddress = ''
      this.addTokenVisible = true
    },
    txFormCheck() {
      const r = {
        success: false
      }
      if (this.addTokenForm.tokenAddress === '') {
        r.msg = this.$t('addToken.form.inputToken')
        return r
      }
      if (null === common.getAddress(this.addTokenForm.tokenAddress)) {
        r.msg = this.$t('addToken.form.addressError')
        return r
      }
      r.success = true
      r.func = this.createPool
      return r
    },
    createPool() {
      if (this.$refs['txConfirm']) {
        this.$refs['txConfirm'].init()
      }
      this.txForm = {
        type: common.getTxTypes().createPool,
        to: this.$store.state.factory.contract.address,
        token: this.addTokenForm.tokenAddress,
      }
      this.txConfirmCall = () => {
        return factory.createPool(this.txForm.token)
      }
      this.txConfirmDialogVisible = true
    }
  }
}
</script>
