  margin-top: 10px;
}
.token-radio-group {
  display: flex;
  flex-direction: column;
}
.token-radio-group .el-radio {
  margin-right: 0;
}
.token-radio-group .el-radio {
  height: 40px;
}
.token-radio-group .el-radio+.el-radio {
  margin-left: 0 !important;
  margin-top: 10px;
}

.token-icon {
  width: 24px;
  height: 24px;
  background-color: rgb(255, 255, 255);
  border-radius: 24px;
  margin-right: 5px;
}

.token-btn {
  width: 100%;
  text-align: start;
  background-color: #111111!important;
